import { Component, OnDestroy, OnInit } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { User } from '@shared-types/user.interface';
import { AppStateInterface } from '@store/app/app-state.interface';
import { Store } from '@ngrx/store';
import { selectCurrentUser, selectIsLoadingAuth } from '@store/auth/auth.selectors';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { updateCurrentUserAction } from '@store/auth/auth.actions';
import { IUserProfileData } from '@ApiClient/backend/User/userProfile';
import { TranslateService } from '@ngx-translate/core';
import { IUserIdentity } from '@ApiClient/backend/User/userIdentity';

@Component({
  selector: 'app-settings',
  templateUrl: 'settings.component.html',
  styleUrls: ['settings.component.scss'],
})
export class SettingsComponent implements OnInit, OnDestroy {
  currentUser$: Observable<User | null> = this.store.select(selectCurrentUser);
  isLoading$: Observable<boolean> = this.store.select(selectIsLoadingAuth);
  currentUserSubscription!: Subscription;
  maxDate!: Date;

  settingsForm = new FormGroup({
    nickname: new FormControl({ value: '', disabled: true }, [Validators.maxLength(15)]),
    email: new FormControl({ value: '', disabled: true }, [Validators.email]),
    userName: new FormControl('', [Validators.maxLength(20)]),
    firstName: new FormControl('', [Validators.maxLength(20)]),
    lastName: new FormControl('', [Validators.maxLength(20)]),
    birthDate: new FormControl(),
    bio: new FormControl(''),
    website: new FormControl(''),
    image: new FormControl(''),
  });
  constructor(
    private store: Store<AppStateInterface>,
    private translate: TranslateService,
  ) {}

  ngOnInit() {
    this.maxDate = new Date();
    this.currentUserSubscription = this.currentUser$.subscribe((user: User | null) => {
      if (user) {
        this.settingsForm.patchValue({
          nickname: user.nicknameUrl,
          email: user.email,
          userName: user.userName,
          firstName: user.firstName,
          lastName: user.lastName,
          birthDate: user.birthDate ? new Date(user.birthDate) : null,
        });
      }
    });
  }
  ngOnDestroy() {
    this.currentUserSubscription.unsubscribe();
  }

  onSubmit() {
    if (this.settingsForm.invalid) {
      this.settingsForm.markAllAsTouched();
      return;
    }

    const userPublic: IUserProfileData = {
      name: this.settingsForm.get('userName')?.value,
      nickname: this.settingsForm.get('nickname')?.value,
      languages: [
        {
          website: this.settingsForm.get('website')?.value,
          biography: this.settingsForm.get('bio')?.value,
          langCode: this.translate.currentLang,
        },
      ],
    } as IUserProfileData;

    const userPrivate: IUserIdentity = {
      firstName: this.settingsForm.get('firstName')?.value,
      lastName: this.settingsForm.get('lastName')?.value,
      birthDate: this.settingsForm.get('birthDate')!.value,
    } as IUserIdentity;

    this.store.dispatch(updateCurrentUserAction({ userPublic, userPrivate }));
  }
}
